export function getDataFromAPI(raid: number,type: number, boss: number, actor: number) {
    return getDataFromStatic(raid, type, boss, actor);
}

export function getDataFromTOCAPI(type: number, boss: number, actor: number) {
    let queryUrl = ServerConfig.QueryTOCAllUrl + "?type=" + type + "&boss=" + boss + "&actor=" + actor;
    return fetch(queryUrl).then(response => {
        return response.json();
    }).catch(exception => {
        return [];
    });
}

