export function getDataFromStatic(raid: number,type: number, boss: number, actor: number): Promise<any> {
    let instance = getInstance(raid, type);
    if (boss > 0) {
        let tempArray = getBossId(raid, boss);
        if (tempArray.length === 2) {
            instance = instance.filter(e=>{ return e.bossid === tempArray[0] || e.bossid === tempArray[1]});
        } else {
            instance = instance.filter(e=>{ return e.bossid === tempArray[0]}); 
        }
    }

    if (actor > 0) {
        instance = instance.filter(e=>{ return e.characterType === actor});
    }

    instance = instance.sort((a,b)=>{ return a.amountpersecond > b.amountpersecond ? -1 : 1})

    let index = 0;
    return Promise.resolve(instance.map(e=>{
        index++;
        return {
            rowIndex: index,
            ...e
        }
    }));
}

function getInstance(raid: number,type: number) {
    if (raid === 3) {
        switch (type) {
            case 1:
                return TOC10N;
            case 2:
                return TOC25N;
            case 3:
                return TOC10H;
            case 4:
                return TOC25H;
            default:
                return TOC10N;
        }
    } else if (raid === 4) {
        switch (type) {
            case 1:
                return ICC10N;
            case 2:
                return ICC25N;
            case 3:
                return ICC10H;
            case 4:
                return ICC25H;
            default:
                return ICC25H;
        }
    } else {
        return ICC25H;
    }
}

function getBossId(raid: number,boss: number):number[] {
    if (raid === 3) {
        switch (boss) {
            case 1:
                return [34797];
            case 2:
                return [34780];
            case 3:
                return [34467, 34451];
            case 4:
                return [34497];
            case 5:
                return [34564];
            default:
                return [34564];
        }
    } else if (raid === 4) {
        switch (boss) {
            case 1:
                return [36612];
            case 2:
                return [36855];
            case 3:
                return [37540, 37215];
            case 4:
                return [37813];
            case 5:
                return [36626];
            case 6:
                return [36627];
            case 7:
                return [36678];
            case 8:
                return [37970];
            case 9:
                return [37955];
            case 10:
                return [36789];
            case 11:
                return [36853];
            case 12:
                return [36597];
            default:
                return [37813];
        }
    } else {
        return [34467, 34451];
    }
}