import React, { useEffect } from 'react';
import { Dropdown, Space, Table, Progress, Popover, ConfigProvider } from 'antd';
import type { MenuProps } from 'antd';

import { DownOutlined } from '@ant-design/icons';
import actorImage from './Resource/Image/actors.jpg';

import 'antd/lib/table/style/css';
import 'antd/lib/progress/style/css';
import 'antd/lib/popover/style/css';
import './Style/AppMobile.css';

import * as AppHelper from "./Utils/AppHelper";
import * as NetHelper from "./Utils/NetHelper";

const raidItems: MenuProps['items'] = AppHelper.getRaidItems();

const characterItems: MenuProps['items'] = AppHelper.getCharacterItems();


function AppMobile() {
  // init
  const [currentRaid, setCurrentRaid] = React.useState(3);
  const [currentIndex, setCurrentIndex] = React.useState(1);
  const diffcultyItems: MenuProps['items'] = AppHelper.getDiffcultyItems(currentRaid);
  const [diffcultyItemsState, setDiffcultyItemsState] = React.useState(diffcultyItems);
  const bossItems: MenuProps['items'] = AppHelper.getBossItems(currentRaid);
  const [bossItemsState, setBossItemsState] = React.useState(bossItems);
  const [rankData, setRankData] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const [maxData, setMaxData] = React.useState(100);
  
  const [currentMode, setCurrentMode] = React.useState(4);
  const [currentBoss, setCurrentBoss] = React.useState(0);
  const [currentActor, setCurrentActor] = React.useState(0);
  let mounted = true;
  useEffect(() => {
    if (mounted) {
      console.log("Mobile");
    }
    return () => {
      mounted = false;
    };
  }, []);

  useEffect(() => {
    if (mounted) {
      setLoading(true);
      NetHelper.getDataFromAPI(currentRaid, currentMode, currentBoss, currentActor).then(data => {
        if (data && data.length > 0) {
          setMaxData(data[0].amountpersecond);
          setRankData(data);
        } else {
          setRankData([]);
        }
      }).finally(() => {
        setCurrentIndex(1);
        setLoading(false);
      });
    }
  }, [currentRaid, currentMode, currentBoss, currentActor]);

  const columnName = [
    {
      title: '排名',
      dataIndex: 'rank',
      key: 'rank',
      width: 60,
      render: (text: any, record: any, index: any) => {
        return <div className='RankDiv'>
          {record.rowIndex}
        </div>;
      },
    },
    {
      title: '姓名',
      dataIndex: 'name',
      key: 'name',
      width: 130,
      render: (text: any, record: any, index: any) => {
        let tempActorClassName = AppHelper.getActorTypeClassName(record.characterType);
        let tempAcotrColor = AppHelper.getActorTypeClassColorName(record.characterType);
        return <>
          <Popover content={AppHelper.getPopOver(record.guild, record.gs)}>
            <div className="ActorDiv">
              <img src={actorImage} className={"Actor " + tempActorClassName}></img>
              <span className={'ActorNameSpan ' + tempAcotrColor}>{text}</span>
            </div>
          </Popover>
        </>;
      },
    },
    {
      title: '秒伤',
      dataIndex: 'amountpersecond',
      key: 'amountpersecond',
      render: (text: any, record: any, index: any) => {
        let tempAcotrColor = AppHelper.getActorTypeColor(record.characterType);
        let tempPercent = Math.round(text / maxData * 100);
        return <>
          <div className='DPSDiv'>
            <Progress style={{ width: "90%", maxWidth: "1100px", minWidth: "200px" }} strokeColor={tempAcotrColor} type="line" percent={tempPercent}
              format={(percent, successPercent) => {
                return text;
              }}
            ></Progress>
          </div>
        </>;
      },
    },
    {
      title: '时间',
      dataIndex: 'date',
      key: 'date',
      width: 140,
      render: (text: any) => {
        return <div className='DateDiv'>
          {text}
        </div>
      },
    }
  ];

  return (
    <div className='App AppMobileBackground'>
      <div className='AppHeader AppMobileBackground'><div className="AppHeaderImg"></div></div>
      <div className='AppContent AppMobileBackground'>
      <div className='Filter'>
          <Dropdown
            className="Dropdown"
            disabled={loading}
            menu={{
              items: raidItems,
              onClick: (event) => {
                let tempKey = parseInt(event.key);
                setCurrentRaid(tempKey);
                let tempDiffItems = AppHelper.getDiffcultyItems(tempKey)
                setDiffcultyItemsState(AppHelper.getDiffcultyItems(tempKey));
                setCurrentMode(tempDiffItems[tempDiffItems.length - 1].key);
                let tempBossitems = AppHelper.getBossItems(tempKey);
                setBossItemsState(tempBossitems);
                setCurrentBoss(0);
              }
            }}
            placement="bottom" arrow>
            <Space>{AppHelper.getRaidFullName(currentRaid)}<DownOutlined /></Space>
          </Dropdown>
          <Dropdown
            className="Dropdown"
            disabled={loading}
            menu={{
              items: diffcultyItemsState,
              onClick: (event) => {
                setCurrentMode(parseInt(event.key));
              }
            }}
            placement="bottom" arrow>
            <Space>{AppHelper.getModeFullName(currentMode)}<DownOutlined /></Space>
          </Dropdown>
          <Dropdown className="Dropdown"
            disabled={loading}
            menu={{
            items: bossItemsState, onClick: (event) => {
              setCurrentBoss(parseInt(event.key));
            }
          }} placement="bottom" arrow>
            <Space>{AppHelper.getBossFullName(currentBoss, currentRaid)}<DownOutlined /></Space>
          </Dropdown>
          <Dropdown className="Dropdown"
            disabled={loading}
            menu={{
            items: characterItems, onClick: (event) => {
              setCurrentActor(parseInt(event.key));
            }
          }} placement="bottom" arrow>
            <Space>{AppHelper.getActorTypeFullName(currentActor)}<DownOutlined /></Space>
          </Dropdown>
        </div>
        <div className='Table'>
          <Table
            loading={loading}
            dataSource={rankData}
            columns={columnName}
            // rowClassName={(record: any, index: any) => {
            //   if (record.rowIndex && record.rowIndex % 2 == 1) {
            //     return "evenRowColor";
            //   } else {
            //     return "oddRowColor";
            //   }
            // }}
            // pagination={false} 
            pagination={{ position: ["bottomCenter"], showSizeChanger: false, current: currentIndex, pageSize: 100, onChange: (page) => { window.scrollTo(0, 0); setCurrentIndex(page); }}}
            rowKey={(record: any) => record.rowIndex} onRow={(record) => {
              return {
                onClick: (event) => { }, // 点击行
                onDoubleClick: (event) => { },
                onContextMenu: (event) => { },
                onMouseEnter: (event) => { }, // 鼠标移入行
                onMouseLeave: (event) => { },
              };
            }} />
        </div>
      </div>
      <div className='AppFooter AppMobileBackground'></div>
    </div>);
}

export default AppMobile;
