import React, { useEffect, useMemo } from 'react';
import { Dropdown, Space, Table, Progress, Popover, Button, message, notification } from 'antd';
import type { MenuProps } from 'antd';
import * as qiniu from 'qiniu-js'

import { DatabaseTwoTone, DownOutlined, DownloadOutlined, MenuOutlined, UploadOutlined } from '@ant-design/icons';
import actorImage from './Resource/Image/actors.jpg';

import 'antd/lib/table/style/css';
import 'antd/lib/progress/style/css';
import 'antd/lib/popover/style/css';
import 'antd/lib/notification/style/css';
import './Style/App.css';

import * as AppHelper from "./Utils/AppHelper";
import * as NetHelper from "./Utils/NetHelper";

const raidItems: MenuProps['items'] = AppHelper.getRaidItems();

const characterItems: MenuProps['items'] = AppHelper.getCharacterItems();

function AppMobile() {
  // init
  const currentPageSize = 100;
  const notificationPlacement = "topRight";
  const [currentRaid, setCurrentRaid] = React.useState(3);
  const [currentIndex, setCurrentIndex] = React.useState(1);
  const diffcultyItems: MenuProps['items'] = AppHelper.getDiffcultyItems(currentRaid);
  const [diffcultyItemsState, setDiffcultyItemsState] = React.useState(diffcultyItems);
  const bossItems: MenuProps['items'] = AppHelper.getBossItems(currentRaid);
  const [bossItemsState, setBossItemsState] = React.useState(bossItems);
  const [rankData, setRankData] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const [menuStatus, setMenuStatus] = React.useState(false);
  const [maxData, setMaxData] = React.useState(100);
  const [currentMode, setCurrentMode] = React.useState(4);
  const [currentBoss, setCurrentBoss] = React.useState(0);
  const [currentActor, setCurrentActor] = React.useState(0);
  let mounted = true;
  useEffect(() => {
    if (mounted) {
      console.log("PC");
    }
    return () => {
      mounted = false;
    };
  }, []);

  useEffect(() => {
    if (mounted) {
      setLoading(true);
      NetHelper.getDataFromAPI(currentRaid, currentMode, currentBoss, currentActor).then(data => {
        if (data && data.length > 0) {
          setMaxData(data[0].amountpersecond);
          setRankData(data);
        } else {
          setRankData([]);
        }
      }).finally(() => {
        setCurrentIndex(1);
        setLoading(false);
      });
    }
  }, [currentRaid, currentMode, currentBoss, currentActor]);

  const columnName = [
    {
      title: '排名',
      dataIndex: 'rank',
      key: 'rank',
      width: 60,
      render: (text: any, record: any, index: any) => {
        return <div className='RankDiv'>
          {record.rowIndex}
        </div>;
      },
    },
    {
      title: '姓名',
      dataIndex: 'name',
      key: 'name',
      width: 130,
      render: (text: any, record: any, index: any) => {
        let tempActorClassName = AppHelper.getActorTypeClassName(record.characterType);
        let tempAcotrColor = AppHelper.getActorTypeClassColorName(record.characterType);
        return <>
          <Popover content={AppHelper.getPopOver(record.guild, record.gs)}>
            <div className="ActorDiv">
              <img src={actorImage} className={"Actor " + tempActorClassName}></img>
              <span className={'ActorNameSpan ' + tempAcotrColor}>{text}</span>
            </div>
          </Popover>
        </>;
      },
    },
    {
      title: '秒伤',
      dataIndex: 'amountpersecond',
      key: 'amountpersecond',
      render: (text: any, record: any, index: any) => {
        let tempAcotrColor = AppHelper.getActorTypeColor(record.characterType);
        let tempPercent = Math.round(text / maxData * 100);
        return <>
          <div className='DPSDiv'>
            <Progress style={{ width: "90%", minWidth: "200px" }} strokeColor={tempAcotrColor} type="line" percent={tempPercent}
              format={(percent, successPercent) => {
                return text;
              }}
            ></Progress>
          </div>
        </>;
      },
    },
    {
      title: '时间',
      dataIndex: 'date',
      key: 'date',
      width: 140,
      render: (text: any) => {
        return <div className='DateDiv'>
          {text}
        </div>
      },
    }
  ];

  const openMenuBlade = () => {
    setMenuStatus(true);
  }

  const closeMenuBlade = () => {
    setMenuStatus(false);
  };

  const fileInputChange = (event: any) => {
    const fileData = event.target.files[0];
    console.log(fileData);
    console.log(fileData.name);
    console.log(fileData.size);
    notification.info({
      message: '正在上传' + fileData.name,
      placement: notificationPlacement,
    });
    NetHelper.getTokenFromAPI().then(response => {
      console.log(response);
      if (!response) {
        notification.error({
          message: fileData.name + "上传失败",
          placement: notificationPlacement,
          duration: null,
        });
        return;
      }
      let uploadSuscrie = qiniu.upload(fileData as any, fileData.name, response.data);
      uploadSuscrie.subscribe({
        complete: ()=>{
          notification.success({
            message: fileData.name + "上传成功",
            placement: notificationPlacement,
            duration: null,
          });
        },
        error: ()=>{
          notification.error({
            message: fileData.name + "上传失败",
            placement: notificationPlacement,
            duration: null,
          });
        }
      });

    })
  }

  return (
    <>
      <div className='App AppBackground'>
        <div className='AppHeader AppBackground'>
          <DatabaseTwoTone className='AppMenu' onClick={() => { openMenuBlade() }} />
          <div className="AppHeaderImg"></div>
        </div>
        <div className='AppContent AppBackground'>
          <div className='Filter'>
            <Dropdown
              className="Dropdown"
              disabled={loading}
              menu={{
                items: raidItems,
                onClick: (event) => {
                  let tempKey = parseInt(event.key);
                  setCurrentRaid(tempKey);
                  let tempDiffItems = AppHelper.getDiffcultyItems(tempKey)
                  setDiffcultyItemsState(AppHelper.getDiffcultyItems(tempKey));
                  setCurrentMode(tempDiffItems[tempDiffItems.length - 1].key);
                  let tempBossitems = AppHelper.getBossItems(tempKey);
                  setBossItemsState(tempBossitems);
                  setCurrentBoss(0);
                }
              }}
              placement="bottom" arrow>
              <Space>{AppHelper.getRaidFullName(currentRaid)}<DownOutlined /></Space>
            </Dropdown>
            <Dropdown
              className="Dropdown"
              disabled={loading}
              menu={{
                items: diffcultyItemsState,
                onClick: (event) => {
                  setCurrentMode(parseInt(event.key));
                }
              }}
              placement="bottom" arrow>
              <Space>{AppHelper.getModeFullName(currentMode)}<DownOutlined /></Space>
            </Dropdown>
            <Dropdown className="Dropdown"
              disabled={loading}
              menu={{
                items: bossItems, onClick: (event) => {
                  setCurrentBoss(parseInt(event.key));
                }
              }} placement="bottom" arrow>
              <Space>{AppHelper.getBossFullName(currentBoss, currentRaid)}<DownOutlined /></Space>
            </Dropdown>
            <Dropdown className="Dropdown"
              disabled={loading}
              menu={{
                items: characterItems, onClick: (event) => {
                  setCurrentActor(parseInt(event.key));
                }
              }} placement="bottom" arrow>
              <Space>{AppHelper.getActorTypeFullName(currentActor)}<DownOutlined /></Space>
            </Dropdown>
          </div>
          <div className='Table'>
            <Table
              loading={loading}
              dataSource={rankData}
              columns={columnName}
              rowClassName={(record: any, index: any) => {
                if (record.rowIndex && record.rowIndex % 2 == 1) {
                  return "evenRowColor";
                } else {
                  return "oddRowColor";
                }
              }}
              pagination={{ position: ["bottomCenter"], showSizeChanger: false, current: currentIndex, pageSize: currentPageSize, onChange: (page) => { window.scrollTo(0, 0); setCurrentIndex(page); } }}
              rowKey={(record: any) => record.rowIndex} onRow={(record) => {
                return {
                  onClick: (event) => { }, // 点击行
                  onDoubleClick: (event) => { },
                  onContextMenu: (event) => { },
                  onMouseEnter: (event) => { }, // 鼠标移入行
                  onMouseLeave: (event) => { },
                };
              }} />
          </div>
        </div>
        <div className='AppFooter AppBackground'></div>
        <div className={menuStatus ? 'AppMenuOpened' : 'AppMenuClosed'} onMouseLeave={() => { closeMenuBlade() }}>
          <div className='AppMenuWrap'>
            {/* <Upload accept='.zip'
              action=''
              onChange={(info) => {
                console.log(info);
                openMessage(info.file.name);
                return false;
              }} showUploadList={false}>

            </Upload> */}
            <input title='upload' placeholder='upload' id='uploadZIP' type='file' accept='.zip' style={{display: "none"}} onChange={fileInputChange} />
            <Button type="primary" shape="default" icon={<UploadOutlined />} size='large' onClick={ ()=> {document.getElementById("uploadZIP")?.click();}}>
              上传记录
            </Button>
            <Button type="primary" style={{ marginTop: "5px" }} shape="default" icon={<DownloadOutlined />} size='large' onClick={() => { window.open("https://pan.baidu.com/s/10ZdKK7RKI08cJ-xPKXyGDg?pwd=2p2a", "_blank") }}>
              下载插件
            </Button>
          </div>
        </div>
      </div>
    </>

  );
}

export default AppMobile;
